.title {
  font-family: CascadiaMono;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin-left: auto;
  margin-right: auto;
}
.btn {
  display: flex;
  justify-content: space-between;
}
@font-face {
  font-family: CascadiaMono;
  src: url(CascadiaMono.ttf);
}
.logo {
  border-left: 2px solid green;
  padding: 4px;
}
